.Nav {
    overflow: hidden;
}
#literally-just-a-blue-banner{
    width: 100vw;
    height: 46px;
    position: fixed;
    top: 0;
    background-color: var(--cyber-darkblue);
    z-index: 0;
    /* gradient color changing from both sides to middle */
    background: linear-gradient(to right, var(--cyber-lavender),var(--cyber-purple), var(--cyber-darkblue), var(--cyber-black), var(--cyber-darkblue), var(--cyber-lavender),var(--cyber-purple));
    background-size: 600% 100%;
    animation: gradient 10s ease infinite;
}
nav {
    float: left;
    width: 100%;
}

.nav-ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: var(--cyber-darkblue);
}

.nav-li {
    float: left;
}

.nav-li a {
    display: block;
    color: var(--cyber-white);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}
.nav-li img {
    /* fit the image to the navbar */
    width: 30px;
    /*background transparent*/
    background-color: transparent;
}
/* move the last element in nav to its right */
.nav-li:last-child {
    position: fixed;
    right: 15px;
    top: -6px;
}
/* move the first element in nav to its left */
.nav-li:first-child {
    left: 0;
    top: 0;
    position: fixed;
}
/* second element in nav */
.nav-li:nth-child(2) {
    position: fixed;
    left: 50vw;
    top: 0;
}
/*second to last element will be next to the last element*/
.nav-li:nth-last-child(2) {
    position: fixed;
    right: 60px;
    top: -6px;
}
#home-button {
    font-size: 1.2rem;
    font-weight: bold;
}
